import { css } from 'glamor';
import * as colors from 'utils/styles/colors';
import * as type from 'utils/styles/type';

export const buttonStyles = ({color, outline, filled, isSmall}: any) => ({
    borderRadius: '100px',
    fontFamily: type.BOLD_FONT,
    letterSpacing: '1.4px',
    appearance: 'none',
    color: outline ? colors[color] : colors.black,
    textTransform: 'uppercase',
    cursor: 'pointer',
    height: isSmall ? null : '64px',
    minHeight: isSmall && 48,
    padding: isSmall ? '0 32px' : '0 64px',
    fontSize: isSmall ? '11px' : '14px',
    border: outline ? '2px solid transparent' : 'none',
    borderColor: outline ? colors[color] : 'transparent',
    position: 'relative',
    zIndex: outline ? 'initial' : '100',
    backgroundColor: filled ? 'none' : outline ? colors.white : colors[color],
    backgroundImage: filled ? colors[`${color}Gradient`] : 'none',
    backgroundClip: 'padding-box',
    transition: 'all .4s ease',

    '&:hover, &:focus': {
        boxShadow: '0 4px 24px 0 rgba(0,0,0,0.16)',
        color: outline ? colors[color] : colors.black,
        backgroundColor: filled ? 'transparent' : colors.white,
    },

    outline: '0 dashed rgba(0,0,0,0.2)',
    '[data-whatinput="keyboard"] &:focus': {
        outlineWidth: '1px',
    },
});



