import * as React from 'react';
import * as glamorous from 'glamorous';
import * as ReactDOM from 'react-dom';
import { connect } from 'react-redux';
import { throttle } from 'lodash';
import { isMobile } from '../../../../state/actionCreators/appCreator';
import { ContentContainer } from './components/ContentContainer';

import { getRetinaBackgroundImageStyle } from 'utils/styles';
import * as colors from 'utils/styles/colors';

import { autobind } from 'core-decorators';

const arrowWhite = require('./assets/cta_arrow_white.svg');
const arrowBlack = require('./assets/cta_arrow_black.svg');

const { findDOMNode } = ReactDOM;
const {
  Div,
  H4,
  H6,
  P,
  Span,
  Img,
} = glamorous;

import {
  cardTitle,
  cardWrapper,
  cardImage,
  cardSubhead,
  creativeSpotlightSubhead,
  cardCTA,
  cardCtaIcon,
  cardContent,
  mobileLogoImage,
  cardSubheadIcon,
  superscript,
} from './base/GridCard-base-styles';

export interface IGridCardProps {
  type: string;
  subhead?: string;
  date?: string;
  title?: string;
  cta: string;
  image: string;
  image2x?: string;
  logoImage?: string;
  logoImage2x?: string;
  featuredImage?: string;
  featuredImage2x?: string;
  titleStyle?: any;
  animate?: boolean;
  isMobile?: any;
  isUpdating?: boolean;
  color?: string;
  lightText?: boolean;
  locale?: string;
  isDesktop?: boolean;
  windowWidth?: number;
  isFeatured?: boolean;
  cardIndex?: number;
  modalId?: number;
  openModal?: any;
  isThreeUp?: any;
}

export interface IGridCardState {
  title: string;
  titleIndex: number;
  subhead: string;
  subHeadIndex: number;
}

const mapStateToProps = (state, ownProps) => ({
  ...ownProps,
  isMobile: state.app.mobile,
  locale: state.data.locale,
});

const cn = (bool, className) => bool ? className : '';

export class GridCardClass extends React.Component<IGridCardProps, IGridCardState> {

  state = {
    title: this.props.title ? this.props.title.split(' ')[0] : '',
    titleIndex: 0,
    subhead: this.props.subhead ? this.props.subhead.split(' ')[0] : '',
    subHeadIndex: 0,
  };

  card;
  titleEl;
  cardEl;

  modalCallback: any;
  gridCardStyles: Array<object>;

  constructor(props) {
    super(props);

    const { type, color, image, image2x, logoImage, logoImage2x, featuredImage, featuredImage2x } = props;

    const backgroundStyles = this.generateBackgroundStyle(type, color);
    const cardImageStyles = this.generateCardImageStyle(type, image, image2x);
    const logoImageStyles = this.generateLogoImageStyle(type, logoImage, logoImage2x);

    this.gridCardStyles = [
      backgroundStyles,
      {
        [' .card-image']: cardImageStyles,
        [' .card-logo-image']: logoImageStyles,
        [' .card-featured-image']: getRetinaBackgroundImageStyle(featuredImage, featuredImage2x),
      },
    ];
  }

  componentDidMount() {
    if (this.props.type === 'creativeSpotlightVideo' && this.cardEl || this.props.type === 'creativeSpotlightDevice' && this.cardEl) {
      this.modalCallback = this.props.openModal.bind(this, this.props.modalId, this.props.type);
      this.cardEl.addEventListener('click', this.modalCallback);
    }
  }

  componentWillUnmount() {
    if (this.props.type === 'creativeSpotlightVideo' || this.props.type === 'creativeSpotlightDevice') {
      this.cardEl.removeEventListener('click', this.modalCallback);
    }
  }

  getInsightsColor(color: string) {
    switch (color) {
      case 'yellow':
        return colors.snapYellow;
      case 'lightYellow':
        return colors.lightYellow;
      case 'orange':
        return colors.regularOrange;
      case 'lightOrange':
        return colors.lightOrange;
      case 'blue':
        return colors.regularBlue;
      case 'lightBlue':
        return colors.lightBlue;
      case 'purple':
        return colors.regularPurple;
      case 'lightPurple':
        return colors.lightPurple;
      case 'red':
        return colors.snapRed;
      case 'lightRed':
        return colors.lightRed;
      case 'green':
        return colors.regularGreen;
      case 'lightGreen':
        return colors.lightGreen;
      default:
        return colors.white;
    }
  }

  generateBackgroundStyle(type, color) {
    let backgroundColor;
    let backgroundImage;
    let backgroudImageRetina;
    let backgroundPosition = 'center';
    let backgroundSize = 'auto';
    if (type === 'newsEntry' || type === 'creativeSpotlightVideo') {
      backgroundImage = `linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url(${this.props.image})`;
      backgroundSize = 'cover';
      backgroudImageRetina = {
        [`@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi)`]: {
          backgroundImage: `linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), url(${this.props.image2x})`,
        },
      };
      if (type === 'newsEntry') {
        backgroundPosition = 'right center';
      }
    } else if (type === 'successStory') {
      backgroundImage = `linear-gradient(${color}, ${color})`;
    } else if (type === 'insightsEntry') {
      backgroundColor = this.getInsightsColor(color);
    } else {
      backgroundImage = `linear-gradient(${color}, ${color})`;
    }

    return {
      backgroundColor,
      backgroundImage,
      backgroundSize,
      backgroundPosition,
      ...backgroudImageRetina,
    };
  }

  generateCardImageStyle(type, image, image2x) {
    const backgroundSize = 'cover';
    const backgroundPosition = 'center center';
    const backgroundImageStyle = getRetinaBackgroundImageStyle(image, image2x);
    if (type !== 'insightsEntry') {
      return {};
    }
    return {
      backgroundSize,
      backgroundPosition,
      ...backgroundImageStyle,
    };
  }

  generateLogoImageStyle(type, image, image2x) {
    let backgroundPosition = 'center center';
    let backgroundSize = 'auto';
    const backgroundImageStyle = getRetinaBackgroundImageStyle(image, image2x);
    if (type === 'successStory' || type === 'creativeSpotlightDevice' || type === 'creativeSpotlightVideo') {
      backgroundPosition = this.props.isMobile ? '32px 2rem' : '44px 2rem';
      backgroundSize = '160px auto';
    }
    return {
      backgroundSize,
      backgroundPosition,
      ...backgroundImageStyle,
    };
  }

  render () {
    const {
      type,
      subhead,
      date,
      title,
      cta,
      logoImage,
      logoImage2x,
      featuredImage,
      titleStyle,
      animate,
      isUpdating,
      color,
      isMobile,
      locale,
      isDesktop,
      windowWidth,
      isFeatured,
      isThreeUp,
    } = this.props;

    const showLogoImage = logoImage && !(type === 'creativeSpotlightDevice' && !isFeatured);
    let fontColor = this.props.lightText ? colors.white : colors.black;
    let arrowColor = this.props.lightText ? arrowWhite : arrowBlack;
    if (type === 'newsEntry' || type === 'creativeSpotlightVideo') {
      fontColor = colors.white;
      arrowColor = arrowWhite;
    }

    return (
      <Div
        innerRef={(el) => { this.cardEl = el; }}
        className={`grid-card ${cn(isFeatured && !isThreeUp, 'featured')} ${cn(animate, 'animate')} ${cn(isUpdating, 'is-updating')} ${type} ${cn(isThreeUp, 'three-up')}`}
        css={[
          cardWrapper,
          ...this.gridCardStyles,
        ]}
        style={{ color: fontColor }}
      >
        <Div
          className={'card-image'}
          css={cardImage}
        >
          {showLogoImage &&  <div className={`card-logo-image ${type}`} /> }
          {featuredImage &&  <div className={`card-featured-image ${type}`} /> }
        </Div>
        <Div className={'card-content'} css={cardContent}>
          {isMobile && type === 'successStory' && logoImage &&
            <Img
              className={'mobile-logo-image'}
              srcSet={`${logoImage ? `${logoImage} 1x, ` : ''}${logoImage2x ? `${logoImage2x} 2x` : ''}`}
              src={logoImage}
              css={mobileLogoImage}
            />
          }
          {subhead &&
            <H6 className={'card-subhead'} css={cardSubhead}>
              <ContentContainer content={date ? `${subhead} / ${date}` : subhead} windowWidth={windowWidth}>
                {type === 'creativeSpotlightVideo' && <Span className={'card-subhead-icon'} css={cardSubheadIcon}/> }
              </ContentContainer>
            </H6>
          }
          {title &&
            <H4 className={'card-title'} css={[titleStyle ? titleStyle(locale) : cardTitle]}>
              <ContentContainer content={title} windowWidth={windowWidth} />
            </H4>
          }
          {cta &&
            <H6 className={'card-cta'} css={cardCTA}>
              { cta }
              <Span css={cardCtaIcon} style={arrowColor && {background: `url(${arrowColor})`}}/>
            </H6>
          }
        </Div>
        {!isFeatured && type === 'creativeSpotlightDevice' && isDesktop &&
          <Span className={'cs-subhead'} css={[cardSubhead, creativeSpotlightSubhead]}>{ subhead }</Span>
        }
      </Div>
    );
  }
}

export const GridCard = connect(mapStateToProps)(GridCardClass);



