import * as React from 'react';
import * as glamorous from 'glamorous';
import * as ReactDOM from 'react-dom';
import { connect } from 'react-redux';
import { isMobile } from '../../../../state/actionCreators/appCreator';
import { Markdown } from '../Markdown';

const { findDOMNode } = ReactDOM;
const {
  Div,
  H4,
  H6,
  P,
  Img,
} = glamorous;

import {
  cardTitle,
  cardWrapper,
  cardImageContainer,
  cardImage,
  imageWrapper,
  cardCopy,
  cardSubhead,
  markdownStyles,
  ctaStyle,
  partnerTypeStyle,
  bottomContainer,
} from './Card-styles';

export interface ICardProps {
  title?: string;
  subhead?: string;
  image: string;
  imageAlt?: string;
  image2x?: string;
  copy: string;
  background?: boolean;
  article?: boolean;
  titleStyle?: any;
  animate?: boolean;
  onRender?: Function;
  isMobile?: any;
  isUpdating?: boolean;
  color?: string;
  locale?: string;
  cardCta?: string;
  partnerType?: string;
}

const mapStateToProps = (state, ownProps) => ({
  ...ownProps,
  isMobile: state.app.mobile,
});

export class CardClass extends React.Component<ICardProps, void> {

  card;

  componentDidMount() {
    if (this.props.onRender) {
      const domNode = findDOMNode(this.card);
      const elHeight = domNode.getBoundingClientRect().height;
      this.props.onRender(elHeight, domNode);
    }
  }

  render () {
    const {
      image,
      image2x,
      imageAlt,
      title,
      subhead,
      copy,
      background,
      article,
      titleStyle,
      animate,
      isUpdating,
      color,
      isMobile,
      locale,
      cardCta,
      partnerType,
    } = this.props;

    return (
      <Div css={cardWrapper({background, article, animate, isUpdating})} ref={ (node) => this.card = node } >
        <Div css={cardImageContainer({ article, color, background })} >
          <Div css={imageWrapper()} >
            <Img
              srcSet={`${image ? `${image} 1x, ` : ''}${image2x ? `${image2x} 2x` : ''}`}
              src={image}
              alt={imageAlt}
              css={cardImage()}
            />
          </Div>
        </Div>
        {subhead &&
          <H6 css={cardSubhead()}>
            { subhead }
          </H6>
        }
        {title &&
          <H4 css={titleStyle ? titleStyle(!copy, locale) : cardTitle(article)}>
            { title }
          </H4>
        }
        {copy ?
          <div style={{ width: '100%' }} >
            <Markdown copy={copy} styleOverrides={markdownStyles(article, background)} />
          </div>
        :
        null
        }
        {(cardCta || partnerType) &&
          <Div css={bottomContainer()}>
            {partnerType &&
              <Div css={partnerTypeStyle()}>
                { partnerType }
              </Div>
            }
            {cardCta &&
              <Div css={ctaStyle(article)}>
                { cardCta }
              </Div>
            }
          </Div>
        }
      </Div>
    );
  }
}

export const Card = connect(mapStateToProps)(CardClass);



